.delete-student-container {
  width: 90%;
  max-width: 600px;
  margin: auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
}

h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
}

.search-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.search-form input {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 10px;
}

.search-form .btn-search {
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-form .btn-search:hover {
  background-color: #0056b3;
}

.error-message {
  color: #dc3545;
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
}

.success-message {
  color: #28a745;
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
}

.student-info {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  margin-bottom: 20px;
}

.student-info p {
  margin: 10px 0;
  font-size: 16px;
  color: #333;
}

.student-info .btn-delete {
  display: block;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  color: #fff;
  background-color: #dc3545;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.student-info .btn-delete:hover {
  background-color: #c82333;
}

/* Responsive design */
@media (max-width: 768px) {
  .search-form input {
    width: 100%;
    max-width: 300px;
  }

  .search-form .btn-search {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .search-form input {
    font-size: 14px;
  }

  .search-form .btn-search {
    font-size: 14px;
  }

  .student-info p {
    font-size: 14px;
  }

  .student-info .btn-delete {
    font-size: 14px;
  }
}
