/* Home Section */
section.home {
    width: 100%;
    min-height: 100vh;
    padding: 100px 5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ecf0f1; /* Light background color */
  }
  
  .home .banner {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  
  .home .banner .banner-desc {
    flex-basis: 50%;
    max-width: 700px;
    z-index: 2;
  }
  
  .banner .banner-desc h2 {
    font-size: 48px;
    color: #2c3e50; /* Dark heading color */
    letter-spacing: 1px;
    line-height: 1.4;
    margin-bottom: 10px;
  }
  
  .banner .banner-desc p {
    color: #34495e; /* Darker text color */
    line-height: 1.6;
    margin-bottom: 50px;
  }
  
  .banner .banner-desc .search-bar {
    position: relative;
    width: 100%;
    max-width: 400px;
    height: 50px;
    display: flex;
    align-items: center;
    overflow: hidden;
  }
  
  .banner-desc .search-bar input {
    width: 100%;
    height: 100%;
    padding: 0 60px 0 10px;
    font-size: 17px;
    letter-spacing: 1px;
    background-color: #ffffff; /* White background for input */
    color: #2c3e50; /* Dark text color */
    border: 2px solid #bdc3c7; /* Light border color */
    border-radius: 5px;
    outline-color: #f39c12; /* Accent color */
  }
  
  .banner-desc .search-bar i {
    position: absolute;
    height: 100%;
    width: 50px;
    background-color: #f39c12; /* Accent color */
    right: 0;
    color: #ffffff; /* White icon color */
    border-radius: 0 5px 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .home .banner .banner-img {
    flex-basis: 50%;
    display: flex;
    justify-content: flex-end;
    position: relative;
    z-index: 1;
  }
  
  .banner-img .banner-img-container {
    position: relative;
    width: 100%;
    max-width: 400px;
  }
  
  .banner-img .banner-img-container img {
    width: 100%;
    height: auto;
  }
  
  .banner-img .banner-img-container .states {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  
  .banner-img-container .states .total-courses {
    position: absolute;
    padding: 10px;
    background-color: #ffffff; /* White background for total courses */
    border-radius: 5px;
    bottom: 15%;
    left: -80px;
    display: flex;
    align-items: center;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  }
  
  .states .total-courses .icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f39c12; /* Accent color */
    margin-right: 10px;
  }
  
  .states .total-courses .icon i {
    font-size: 22px;
    color: #ffffff; /* White icon color */
  }
  
  .states .total-courses .desc {
    display: flex;
    flex-direction: column;
  }
  
  .states .total-courses .desc span:nth-child(1) {
    font-size: 24px;
    font-weight: 600;
    color: #2c3e50; /* Dark heading color */
  }
  
  .states .total-courses .desc span:nth-child(2) {
    font-size: 15px;
    color: #34495e; /* Darker text color */
  }
  
  .banner-img-container .states .courses-ratings {
    position: absolute;
    width: 90px;
    height: 90px;
    padding: 10px;
    border-radius: 50%;
    background-color: #ffffff; /* White background for ratings */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 80px;
    right: 5px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  }
  
  .states .courses-ratings span:nth-child(1) {
    color: #2c3e50; /* Dark heading color */
    font-size: 24px;
    font-weight: 600;
    text-align: center;
  }
  
  .states .courses-ratings span:nth-child(1) i {
    color: gold;
    font-size: 14px;
  }
  
  .states .courses-ratings span:nth-child(2) {
    color: #34495e; /* Darker text color */
    font-size: 15px;
    text-align: center;
  }
  
  .banner-img .banner-img-container .pattern {
    position: absolute;
    top: 20px;
    left: -40px;
    z-index: -1;
  }
  
  .banner-img-container .pattern img {
    width: 150px;
    height: auto;
    opacity: 0.4; /* Slightly visible pattern */
  }
  
  /* Responsive Design */
  @media (max-width: 968px) {
    .home .banner {
      flex-direction: column-reverse;
      justify-content: flex-start;
      align-items: flex-start;
    }
  
    .home .banner .banner-desc,
    .home .banner .banner-img {
     margin-top: 20%;
      flex-basis: 100%;
    }
  
    .home .banner .banner-img {
      align-self: center;
      margin-bottom: 50px;
    }
  }
  
  @media (max-width: 768px) {
    .banner-img-container .states .total-courses {
      left: 5px;
      bottom: 10%;
    }
  
    .banner .banner-desc h2 {
      font-size: 42px;
    }
  }
  
  @media (max-width: 468px) {
    .banner-img .banner-img-container {
      width: 100%;
      z-index: -1;
    }
  
    .banner .banner-desc .search-bar {
      width: 100%;
    }
  
    .banner-img-container .states .courses-ratings {
      top: 30px;
      right: -10px;
    }
  
    .banner .banner-desc h2 {
      font-size: 32px;
    }
  
    .main-navbar .get-started-btn-container {
      display: none;
    }
  }
  