/* Footer Section */
section.footer {
	width: 100%;
	padding: 70px 5%;
	background-color: #2c3e50; /* Dark background color for better contrast */
  }
  
  /* Footer Contents */
  .footer .footer-contents {
	width: 100%;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-bottom: 50px;
  }
  
  .footer-contents .footer-col {
	flex-basis: calc(calc(100% / 4) - 20px);
	margin-bottom: 30px;
  }
  
  .footer-contents .footer-col .col-title {
	display: flex;
	align-items: center;
	margin-bottom: 20px;
  }
  
  .footer-col .col-title img {
	width: 120px;
	height: auto; /* Ensure the image scales proportionally */
  }
  
  .footer-col .col-title h3 {
	font-size: 24px;
	color: #ecf0f1; /* Light color for headings */
  }
  
  /* Footer Column Contents */
  .footer-col .col-contents {
	display: flex;
	flex-direction: column;
  }
  
  .footer-col-1 .col-contents p {
	color: #bdc3c7; /* Light gray text color */
	font-size: 15px;
	line-height: 1.6;
	margin-bottom: 15px;
  }
  
  .footer-col-2 .col-contents .contact-row {
	margin-bottom: 15px;
	display: flex;
	justify-content: space-between;
  }
  
  .footer-col-2 .col-contents .contact-row span {
	color: #ecf0f1; /* Light text color for contact details */
	font-size: 16px;
  }
  
  .footer-col-2 .col-contents .contact-row span:nth-child(1) {
	color: #f39c12; /* Accent color for labels */
	font-size: 17px;
	font-weight: 500;
	margin-bottom: 2px;
  }
  
  .footer-col-3 .col-contents a {
	text-decoration: none;
	color: #bdc3c7; /* Light gray text color for links */
	margin-bottom: 10px;
	display: block;
	transition: color 0.3s, letter-spacing 0.3s;
  }
  
  .footer-col-3 .col-contents a:hover {
	color: #f39c12; /* Accent color on hover */
	letter-spacing: 1px;
  }
  
  /* Newsletter Signup */
  .footer-col-4 .col-contents input {
	width: calc(100% - 30px); /* Adjust width to account for padding and borders */
	padding: 15px;
	background-color: #34495e; /* Darker background for input */
	color: #ecf0f1; /* Light text color */
	border: none;
	border-radius: 5px;
	margin-bottom: 10px; /* Space between input and button */
  }
  
  .footer-col-4 .col-contents .newsletter-btn {
	background-color: #f39c12; /* Accent color for button */
	color: #fff;
	padding: 15px;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	font-size: 16px;
	transition: background-color 0.3s, color 0.3s;
  }
  
  .footer-col-4 .col-contents .newsletter-btn:hover {
	background-color: transparent;
	color: #f39c12; /* Accent color text on hover */
	border: 1px solid #f39c12; /* Accent color border on hover */
  }
  
  /* Copyright Section */
  .footer .copy-rights {
	padding: 20px 0;
	border-top: 1px solid #bdc3c7; /* Light gray border */
	text-align: center;
  }
  
  .footer .copy-rights p {
	color: #ecf0f1; /* Light text color */
  }
  
  .footer .copy-rights p b {
	color: #f39c12; /* Accent color for bold text */
  }
  
  /* Responsive Design */
  @media (max-width: 968px) {
	.footer-contents .footer-col {
	  flex-basis: calc(calc(100% / 2) - 20px);
	}
  }
  
  @media (max-width: 568px) {
	.footer-contents .footer-col {
	  flex-basis: 100%;
	}
  
	.footer-col-4 .col-contents input {
	  width: 100%; /* Full width for small screens */
	}
  }
  