/* General Styling for the Services Section */
.services {
	width: 100%;
	padding: 100px 5%; /* Adjusted padding for better spacing on different screen sizes */
	background-color: #f9f9f9; /* Light background color for the section */
  }
  
  /* Header Styling */
  .services .section-header {
	text-align: center;
	margin-bottom: 40px;
  }
  
  .services .section-header h1 {
	font-size: 2.5em;
	color: #333; /* Darker text color for the header */
  }
  
  .services .section-header p {
	font-size: 1.1em;
	color: #666; /* Lighter text color for the description */
  }
  
  /* Styling for Service Contents */
  .services .services-contents {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	gap: 20px; /* Added gap between items for better spacing */
  }
  
  /* Service Box Styling */
  .services-contents .service-box {
	flex: 1 1 calc(33.333% - 20px); /* Responsive width with gap consideration */
	padding: 25px 20px;
	border-radius: 10px; /* Rounded corners for service boxes */
	background-color: #fff; /* White background for service boxes */
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
	transition: all 0.3s ease-in-out; /* Smooth transition for hover effects */
	text-align: center; /* Center text inside service box */
  }
  
  /* Hover Effect for Service Boxes */
  .services-contents .service-box:hover {
	box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15); /* Enhanced shadow on hover */
	transform: translateY(-5px); /* Slight lift effect on hover */
  }
  
  /* Styling for Service Icons */
  .service-box .service-icon {
	width: 60px; /* Icon size */
	height: 60px;
	display: flex;
	justify-content: center; /* Center align icon horizontally */
	align-items: center; /* Center align icon vertically */
	border-radius: 50%;
	background-color: var(--primary-clr); /* Background color */
	color: var(--light-heading-clr); /* Icon color */
	font-size: 2.5em; /* Font size for icons */
	margin: 0 auto 20px; /* Center the icon horizontally and space below */
  }
  
  /* Icon Font Size Adjustments */
  .service-box .service-icon i {
	font-size: inherit; /* Use the font size from .service-icon */
	color: inherit; /* Inherit color from parent */
  }
  
  /* Service Box Description Styling */
  .service-box .service-desc {
	text-align: center; /* Center text in service description */
  }
  
  .service-box .service-desc h2 {
	margin-bottom: 10px;
	color: #333; /* Darker color for headings */
	font-size: 1.5em; /* Larger font size for headings */
  }
  
  .service-box .service-desc p {
	line-height: 1.6;
	color: #555; /* Slightly lighter text color for paragraphs */
  }
  
  /* Color Variations for Different Service Boxes */
  .service-box:nth-child(1) .service-icon {
	background-color: #fef0f0; /* Light pink background */
  }
  
  .service-box:nth-child(1) .service-icon i {
	color: #e57373; /* Pink color */
  }
  
  .service-box:nth-child(2) .service-icon {
	background-color: #e8f5e9; /* Light green background */
  }
  
  .service-box:nth-child(2) .service-icon i {
	color: #66bb6a; /* Green color */
  }
  
  .service-box:nth-child(3) .service-icon {
	background-color: #e3f2fd; /* Light blue background */
  }
  
  .service-box:nth-child(3) .service-icon i {
	color: #42a5f5; /* Blue color */
  }
  
  /* Responsive Styles */
  @media (max-width: 968px) {
	.services-contents .service-box {
	  flex: 1 1 calc(50% - 20px); /* Two items per row on medium screens */
	}
  }
  
  @media (max-width: 668px) {
	.services-contents .service-box {
	  flex: 1 1 100%; /* Full width on small screens */
	}
  }
  