/* Container for the entire component */
.edit-student-container {
  width: 90%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Title styling */
.edit-student-container h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
}

/* Styling for the search form */
.search-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.search-form input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 400px;
}

.search-form .btn-search {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.search-form .btn-search:hover {
  background-color: #0056b3;
}

/* Error message styling */
.error-message {
  color: #e74c3c;
  text-align: center;
  margin: 10px 0;
  font-size: 16px;
}

/* Styling for the form */
.edit-student-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

.form-group input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/* Styling for the update button */
.btn-update {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.btn-update:hover {
  background-color: #218838;
}

/* Responsive design */
@media (max-width: 768px) {
  .search-form input {
    width: 100%;
    max-width: 300px;
  }

  .search-form .btn-search {
    width: 100%;
    max-width: 300px;
  }

  .form-group input {
    font-size: 14px;
  }

  .btn-update {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .search-form input {
    font-size: 14px;
  }

  .search-form .btn-search {
    font-size: 14px;
  }

  .form-group input {
    font-size: 14px;
  }

  .btn-update {
    font-size: 14px;
  }
}
