/* Container for the whole login page */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #f0f4f8, #e1e5ea); /* Subtle gradient background */
  animation: backgroundAnimation 15s infinite alternate; /* Animation for background */
}

.login-container {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 100%;
  max-width: 400px;
  text-align: center;
  animation: fadeIn 2s ease-in-out; /* Animation for fade-in effect */
}

h2 {
  color: #333;
  font-size: 26px;
  margin-bottom: 20px;
  font-weight: 600;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

label {
  display: block;
  color: #555;
  margin-bottom: 8px;
  font-size: 14px;
}

input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.2);
}

.btn-login {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 12px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.btn-login:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.btn-login:active {
  background-color: #004080;
}

/* Responsive adjustments */
@media(max-width: 768px) {
  .login-container {
    padding: 15px;
    width: 90%;
    max-width: 350px;
  }

  h2 {
    font-size: 22px;
  }
}

@keyframes backgroundAnimation {
  0% {
    background: linear-gradient(135deg, #f0f4f8, #e1e5ea);
  }
  100% {
    background: linear-gradient(135deg, #e1e5ea, #f0f4f8);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
