/* General Styling for MainPage */
.main-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #f4f4f4; /* Light gray background for the main page */
  }
  
  .main-page > div {
    width: 100%;
    max-width: 1200px; /* Max width to prevent overly wide content */
    margin: 20px 0; /* Vertical spacing between sections */
    padding: 20px;
    border-radius: 10px;
    background-color: #fff; /* White background for sections */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for sections */
  }
  
  /* Section Specific Styles */
  .home {
    background-color: #3498db; /* Blue background for Home section */
    color: #fff; /* White text color */
  }
  
  .service {
    background-color: #2ecc71; /* Green background for Service section */
    color: #fff; /* White text color */
  }
  
  .course {
    background-color: #e67e22; /* Orange background for Course section */
    color: #fff; /* White text color */
  }
  
  .categories {
    background-color: #9b59b6; /* Purple background for Categories section */
    color: #fff; /* White text color */
  }
  
  .footer {
    background-color: #34495e; /* Darker background for Footer section */
    color: #fff; /* White text color */
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .main-page {
      padding: 10px;
    }
  
    .main-page > div {
      padding: 15px;
      margin: 10px 0; /* Reduced margin on smaller screens */
    }
  
    .home, .service, .course, .categories, .footer {
      background-color: #fff; /* White background on mobile for better readability */
      color: #333; /* Darker text color for better contrast */
    }
  }
  