/* Container for the entire dashboard */
.dashboard-container {
  width: 90%;
  max-width: 1000px;
  margin: auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff; /* Clean white background */
}

/* Header styling */
h2 {
  text-align: center;
  color: #2c3e50; /* Dark grey for heading */
  margin-bottom: 30px;
  font-size: 2em; /* Larger font size for prominence */
}

/* Container for statistics */
.stats {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  flex-wrap: wrap; /* Adjusts layout for smaller screens */
}

/* Individual stat item */
.stat-item {
  flex: 1 1 calc(50% - 20px); /* Responsive flex basis for 2 items per row */
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9; /* Slightly off-white background */
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s; /* Smooth transitions */
}

/* Hover effect for stat items */
.stat-item:hover {
  transform: translateY(-5px); /* Slightly raise the item */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Stronger shadow */
}

/* Styling for stat item headings */
.stat-item h3 {
  margin-bottom: 10px;
  color: #3498db; /* Blue for headings */
  font-size: 20px; /* Slightly larger font size */
}

/* Styling for stat item values */
.stat-item p {
  font-size: 32px;
  color: #34495e; /* Dark grey for text */
  font-weight: bold;
}

/* Error message styling */
.error-message {
  color: #e74c3c; /* Red for error messages */
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
}

/* Success message styling */
.success-message {
  color: #2ecc71; /* Green for success messages */
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .stat-item {
      flex: 1 1 calc(100% - 20px); /* Full width for smaller screens */
  }
}

@media (max-width: 480px) {
  .dashboard-container {
      padding: 15px;
  }
  
  .stat-item h3 {
      font-size: 18px; /* Slightly smaller heading on smaller screens */
  }

  .stat-item p {
      font-size: 28px; /* Slightly smaller text on smaller screens */
  }
}
