/* Common styles for the student form and login page */
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.student-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    background-color: #f4f4f4;
}

/* Styles specific to the student form container */
.student-form {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 100%;
    max-width: 400px; /* Adjusted width for larger screens */
    margin: auto;
}

/* Header styles */
.student-form h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

/* Form group styles */
.student-form .form-group {
    margin-bottom: 15px;
}

.student-form .form-group label {
    display: block;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
}

.student-form .form-group input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
    margin-top: 5px;
}

/* Button styles */
.student-form button {
    background-color: #4caf50;
    color: #fff;
    padding: 10px;
    width: 100%;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    margin-top: 10px;
}

.student-form button:hover {
    background-color: #45a049;
}

/* Responsive styles */
@media (max-width: 600px) {
    .student-form-container {
        height: auto; /* Adjust height for smaller screens */
        padding: 20px; /* Add padding to the container */
    }

    .student-form {
        padding: 15px;
        width: 100%;
        max-width: 100%; /* Ensure form takes full width on small screens */
    }

    .student-form h2 {
        font-size: 18px; /* Slightly smaller font size for the header */
    }

    .student-form .form-group input {
        font-size: 16px; /* Adjust font size for inputs */
        padding: 10px; /* Increase padding for better touch experience */
    }

    .student-form button {
        font-size: 16px; /* Adjust font size for the button */
        padding: 12px; /* Increase padding for the button */
    }
}
