/* Define color variables for consistent styling */
:root {
    --primary-clr: #3498db; /* Primary color */
    --primary-light-clr: #ecf0f1; /* Light variant of the primary color */
    --dark-heading-clr: #2c3e50; /* Dark color for headings */
    --dark-text-clr: #7f8c8d; /* Dark color for text */
    --light-heading-clr: #fff; /* Light color for icons */
}

/* Section styling */
section.categories {
    width: 100%;
    padding: 100px 8% 20px;
    background-color: #f4f4f4; /* Light background for the section */
}

.categories .section-header {
    text-align: center;
    margin-bottom: 40px;
}

.section-header h1 {
    font-size: 28px;
    color: var(--dark-heading-clr);
    margin-bottom: 10px;
}

.section-header p {
    font-size: 16px;
    color: var(--dark-text-clr);
}

/* Container for category items */
.categories .categories-contents {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

/* Category item styling */
.categories-contents .category-item {
    flex-basis: calc((100% / 4) - 1%);
    background-color: var(--primary-light-clr);
    padding: 25px 15px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    margin-bottom: 20px;
}

/* Hover effect for category item */
.categories-contents .category-item:hover {
    box-shadow: 0px 0px 10px rgba(32, 130, 69, 0.2);
    transform: translateY(-3px);
}

/* Icon styling within category item */
.category-item .category-icon {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-clr);
    margin-bottom: 15px;
}

.category-item .category-icon svg {
    color: var(--light-heading-clr);
    font-size: 24px;
}

/* Description styling within category item */
.category-item .category-desc {
    text-align: center;
}

.category-item .category-desc h3 {
    color: var(--dark-heading-clr);
    font-size: 22px;
    margin-bottom: 10px;
}

.category-item .category-desc p {
    color: var(--dark-text-clr);
    font-size: 16px;
    line-height: 1.5;
}

/* Responsive styling */
@media (max-width: 968px) {
    .categories-contents .category-item {
        flex-basis: calc((100% / 2) - 1%);
        margin-bottom: 15px;
    }
}

@media (max-width: 568px) {
    .categories-contents .category-item {
        flex-basis: 100%;
    }
}
